/* Contenedor de la página principal */
.home-container {
  text-align: center;
}

.titulo {
  font-size: 32px;
  margin-bottom: 5px;
  color: #333;
}

.subtitulo {
  margin-bottom: 30px;
  font-size: 18px;
  color: #555;
}

/* Contenedor de tarjetas */
.bank-cards {
  display: flex;
  justify-content: center;
  gap: 30px;
}

/* Tarjeta */
.bank-card {
  background: white;
  width: 260px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.bank-card h2 {
  margin-bottom: 15px;
  color: #333;
}

/* Botón */
.bank-card button,
.volver-btn {
  padding: 10px 15px;
  font-size: 16px;
  border: none;
  background-color: #007bff;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}

.bank-card button:hover,
.volver-btn:hover {
  background-color: #0056b3;
}

/* Contenedor del panel del banco */
.panel-container {
  padding: 20px;
}
